.search {
  max-width: 1440px;
  margin: 0 auto;
  padding: 160px 0 80px 0;
  position: relative;
  background-image: url('../../images/backgrounds/searchform-background.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.search__form {
  max-width: 608px;
  margin: 0 auto;
}

.search__title {
  margin: 0;
  font-family: 'Robotos', 'Arial', sans-serif;
  font-size: 60px;
  line-height: 64px;
  font-style: normal;
  color: white;
}

.search__subtitle {
  max-width: 545px;
  margin: 32px 0 0 0;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-size: 18px;
  line-height: 24px;
  color: white;
}

.search__wrapper {
  height: 64px;
  margin: 64px 0 0 0;
  padding: 0;
  display: flex;
  background-color: white;
  border-radius: 100px;
  position: relative;
}

.search__input {
  width: 100%;
  height: 24px;
  place-self: center;
  margin-left: 24px;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #1A1B22;
  border: none;
  outline: none;
  text-overflow: ellipsis;
}

.search__input:focus {
  font-style: italic;
}

.search__button {
  width: 168px;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  color: white;
  background-color:#2F71E5;
  border-radius: 100px;
  box-shadow: 0 5px 15px rgba(14, 26, 57, 0.2);
  outline: none;
}

.search__button:hover{
  background-color: #347EFF;
  cursor: pointer;
}

.search__button:focus {
  background-color: #2A65CC;
}

@media screen and (max-width: 900px) {
  .search {
    padding: 104px 0 48px;
  }

  .search__form {
    max-width: 452px;
  }

  .search__title {
    max-width: 385px;
    font-size: 36px;
    line-height: 40px;
  }

  .search__subtitle {
    max-width: 448px;
    font-size: 18px;
    line-height: 24px;
    margin-top: 16px;
  }

  .search__wrapper {
    margin: 48px 0 0 0;
    height: 56px;
  }

}


@media screen and (max-width: 550px) {

  .search {
    height: 450px;
    background-image: url('../../images/backgrounds/searchform-background-small.png');

  }

  .search__form {
    max-width: 285px;
    margin: 0 auto;
  }

  .search__wrapper {
    display: block;
    margin-top: 122px;
    margin-bottom: 0;
  }

  .search__input {
    max-width: 250px;
    margin: 17px 16px;
  }

  .search__button {
    width: 100%;
    margin-top: 16px;
    height: 56px;
  }

}
